  /* Back to top button */
  
  .back-to-top {
    position: fixed;
    display: none;
    background: #708da6;
    color: #fff;
    width: 44px;
    height: 44px;
    text-align: center;
    line-height: 1;
    font-size: 16px;
    border-radius: 50%;
